.listBlocks{
    margin: 0 auto;
    min-height: 520px;
    min-width: 90%;
    text-align: center;
    padding-top: 10px;
}
.listBlocks > .block{
    background-color: rgba(2, 16, 54, 0.877);
    color: #040f24;
    display: inline-block;
    font-size: 10px;
    font-weight: bold;
    margin: 0 2px;
    text-align: center;
    transition-duration: 150ms;
    transition-property: height;
    transition-timing-function: ease-in;
}
.listBlocks > .block:hover{
 box-shadow: 0px 5px 20px #ffffff;   
}
