nav{
    align-items: center;
    background-color: #292940;
    box-shadow: 0px 5px 10px rgb(245, 239, 239);
    display: flex;
    justify-content: space-between;
    min-height: 100px;
    padding: 0 20px;
}

nav .nav-brand{
    color: white;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    margin-right:20% ;
    margin-left: 2%;
        font-size: 2.5em;
}
