.toolbox{
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.toolbox label{
    color: white;
    display: block;
    text-align: center;
}

.toolbox > div{
    display: flex;
    justify-content: space-between;
    margin-right: 2%;
}

.toolbox button{
    background-color: #35ba00;
    border-radius: 13px;
    border: none;
    color: #021402;
    cursor: pointer;
    font-size: 1em;
    font-weight: bold;
    margin: 0 10px;
    outline: none;
    padding: 13px 10px;
}
.toolbox button:hover {
    color: #111;
            background: #39ff14;
            box-shadow: 0 0 50px #39ff14;
}
.toolbox select, input[type='range']{
    cursor: pointer;
    margin: 0 10px;
}

.toolbox input, select, button{
    min-width: 130px;
}

.toolbox select {
    background: #35ba00;
    border: none;
    color: rgb(19, 1, 1);
    font-size: 0.9em;
    outline: none;
    font-weight: bold;
    padding: 5px 5px;
}
.toolbox select:hover {
    color: #111;
            background: #39ff14;
            box-shadow: 0 0 50px #39ff14;
}


@media(max-width: 992px){
    nav{
        flex-direction: column;
    }
    
    .toolbox{
        display: block;
        text-align: center;
    }
    .toolbox > div{
        margin: 10px 0;
    }

    .speed{
        display: none;
    }
}

@media(max-width:1100px){
    nav .nav-brand{
        margin-right: 2%;   
        margin-left: 0%;
       }
    
       img{
          display: none; 
       }
}
